import axios from 'axios';
import React, { useEffect, useState } from 'react';
import moment from 'moment'

import { LAB_BOOKING_ALL_API } from '../../components/constants.js';
import Datatable from '../../components/datatable/Datatable';
import { getBearerHeaderValue } from '../../service/userservice';

const tableHeaders = [
    { field: 'id', headerName: 'ID', width: 70 },
    { field: 'bookingDateTime', headerName: 'Booking Date', width: 200,
    renderCell: (params) => {
        const date= moment(params.dateTime).format('dd-MM-yyyy h:mma');
        const formatDate = date;
        return (
            formatDate
        )
    }},
    { field: 'status', headerName: 'Status', width: 130},
    { field: 'patient.name', headerName: 'Patient name', width: 130,
    renderCell: (params) => {
        return (
            params.row.patient.name
        )
    } },
    { field: 'patient.gender', headerName: 'Gender', width: 130 ,
    renderCell: (params) => {
        return (
            params.row.patient.gender
        )
    }},
    { field: 'patient.phone', headerName: 'Phone', width: 130,
    renderCell: (params) => {
        return (
            params.row.patient.phone
        )
    } },
    { field: 'patient.email', headerName: 'Email', width: 130,
    renderCell: (params) => {
        return (
            params.row.patient.email
        )
    } },
    {
        field: 'patient.address',
        headerName: 'Address',
        sortable: false,
        width: 130,
        renderCell: (params) => {
            return (
                params.row.patient.address
            )
        }
    }
];

const LabBookingsDataTable = () => {
    const [labBookings, setLabBookings] = useState([]);

    const getLabBookings = async () => {
        const response = axios.get(LAB_BOOKING_ALL_API,
            {
                headers: {
                    'Authorization': getBearerHeaderValue()
                }
            }).then(
                res => {
                    setLabBookings(res.data.data);
                }
            ).catch(err => {
                console.log(err);
            });
    }

    useEffect(() => {
        getLabBookings()
    }, []);

    return (
        <>
            <Datatable columns={tableHeaders} rows={labBookings} />
        </>
    )
}

export default LabBookingsDataTable