import React from 'react'
import { Navbar } from '../../components/navbar/Navbar'
import { Sidebar } from '../../components/sidebar/Sidebar'

import { DriveFolderUploadOutlined } from '@mui/icons-material'
import './newPatient.scss'

import { PATIENTS_CREATE_API } from '../../components/constants'
import { getBearerHeaderValue } from '../../service/userservice'

import axios from 'axios'
import { useState } from 'react'

import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { failureNotify, successNotify } from '../../components/toaster/toastNotification'

const NewPatient = () => {

    const [gender, setGender] = useState('M');
    const [name, setName] = useState(null);
    const [email, setEmail] = useState(null);
    const [phone, setPhone] = useState(null);
    const [address, setAddress] = useState(null);
    const [bloodGroup, setBloodGroup] = useState(null);
    const [alternatePhone, setAlternatePhone] = useState(null);
    const [patientType, setPatientType] = useState(null);
    const [age, setAge] = useState(null);
    const status = 'TRUE';

    function save(event) {
        event.preventDefault();

        let item = { patientType, gender, name, email, phone, address, bloodGroup, alternatePhone, status, age };
        const response = axios.post(PATIENTS_CREATE_API, item,
            {
                headers: {
                    'Authorization': getBearerHeaderValue()
                }
            }).then(
                res => {
                    successNotify('Patient registered!');
                }
            ).catch(err => {
                console.log(err);
                failureNotify('Register patient failed!');
            });
    }


    return (
        <div>
            <div className='new'>
                <Sidebar />
                <div className="newContainer">
                    <Navbar />
                    <div className="top">
                        <h1>Register Patient</h1>
                    </div>
                    <div className="bottom">
                        <div className="left">
                            <img src="https://icon-library.com/images/no-image-icon/no-image-icon-0.jpg" alt="" />
                        </div>
                        <div className="right">
                            <form onSubmit={save}>
                                <div className="formInput">
                                    <label htmlFor="file">Image: <DriveFolderUploadOutlined className="icon" /></label>
                                    <input type="file" id="file" style={{ display: "none" }} />
                                </div>
                                <div className="formInput">
                                    <label>Type</label>
                                    <select onChange={(e) => {
                                        setPatientType(e.target.value)
                                    }} >
                                        <option value="OUTPATIENT">Out Patient</option>
                                        <option value="INPATIENT">In Patient</option>
                                        <option value="EMERGENCY">Emergency</option>
                                    </select>
                                </div>
                                <div className="formInput">
                                    <label>Gender</label>
                                    <select onChange={(e) => {
                                        setGender(e.target.value)
                                    }} >
                                        <option value="M">Male</option>
                                        <option value="F">Female</option>
                                        <option value="O">Other</option>
                                    </select>
                                </div>
                                <div className="formInput">
                                    <label>Name</label>
                                    <input type="text" placeholder="Full name"
                                        onKeyUp={(e) => {
                                            setName(e.target.value)
                                        }} />
                                </div>
                                <div className="formInput">
                                    <label>Phone</label>
                                    <input type="text" placeholder="Phone"
                                        onKeyUp={(e) => {
                                            setPhone(e.target.value)
                                        }} />
                                </div>
                                <div className="formInput">
                                    <label>Alternate Phone</label>
                                    <input type="text" placeholder="Alt phone"
                                        onKeyUp={(e) => {
                                            setAlternatePhone(e.target.value)
                                        }} />
                                </div>
                                <div className="formInput">
                                    <label>Email</label>
                                    <input type="text" placeholder="Email"
                                        onKeyUp={(e) => {
                                            setEmail(e.target.value)
                                        }} />
                                </div>
                                <div className="formInput">
                                    <label>Address</label>
                                    <input type="text" placeholder="Address"
                                        onKeyUp={(e) => {
                                            setAddress(e.target.value)
                                        }} />
                                </div>
                                <div className="formInput">
                                    <label>Age</label>
                                    <input type="number" placeholder="Age"
                                        onKeyUp={(e) => {
                                            setAge(e.target.value)
                                        }} />
                                </div>
                                <div className="formInput">
                                    <label>Blood Grp</label>
                                    <input type="text" placeholder="Blood grp"
                                        onKeyUp={(e) => {
                                            setBloodGroup(e.target.value)
                                        }} />
                                </div>
                                <div className="formInput"></div>
                                <button>Register</button>

                            </form>
                        </div>
                    </div>
                </div >
            </div >
            <ToastContainer />
        </div>
    )
}

export default NewPatient