import axios from 'axios'
import React, { useState } from 'react'
import { useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { ToastContainer } from 'react-toastify'

import { LAB_BOOKING_ONE_API } from '../../components/constants'
import { Navbar } from '../../components/navbar/Navbar'
import { Sidebar } from '../../components/sidebar/Sidebar'
import { getBearerHeaderValue } from '../../service/userservice'
import './labBookingUpdate.scss'

const LabBookingUpdate = () => {
    const id = useParams().bookingId;

    const [labBooking, setLabBooking] = useState([]);
    const [labBookingDetails, setLabBookingDetails] = useState([]);

    useEffect(() => {
        async function getLabBooking() {
            const response = axios.get(LAB_BOOKING_ONE_API.replace('{id}', id),
                {
                    headers: {
                        'Authorization': getBearerHeaderValue()
                    }
                }).then(
                    res => {
                        setLabBooking(res.data.data);
                        setLabBookingDetails(res.data.data.labBokingDetails);
                    }
                ).catch(err => {
                    console.log(err);
                });
        }

        getLabBooking();
    }, []);

    return (
        <div>
            <div className='new'>
                <Sidebar />
                <div className="newContainer">
                    <Navbar />
                    <div className="level1">
                        <h1>Update Lab Booking</h1>
                    </div>

                    <div className="level2">

                    </div>
                    <div className="level3">

                    </div>
                    <div className="level4">
                        <form>
                            
                            <button>Update</button>

                        </form>
                    </div>
                </div >
            </div >
            <ToastContainer />
        </div >
    )
}

export default LabBookingUpdate