import axios from 'axios';
import React, { useState } from 'react';
import AsyncSelect from 'react-select/async';
import { getBearerHeaderValue } from '../../service/userservice';
import { LAB_TEST_ALL_API } from '../constants';

const SelectLabTest = ({handleChange, sharedValue}) => {
    const [query, setQuery] = useState("");
    const [value, setSelectedValue] = useState(null);

    // load options using API call
    const loadOptions = () => {
        const response = axios.get(LAB_TEST_ALL_API+ '?query='+query,
            {
                headers: {
                    'Authorization': getBearerHeaderValue()
                }
            }).then(
                res => {
                    return res.data.data;
                }
            ).catch(err => {
                console.log(err);
            });

        return response;
    };

    return (
        <>
            <AsyncSelect
                cacheOptions
                defaultOptions
                value={sharedValue}
                getOptionLabel={e => e.testName}
                getOptionValue={e => e.id}
                loadOptions={loadOptions}
                onInputChange = {(value)=> setQuery(value)}
                onChange={handleChange}
            />
        </>
    );
}

export default SelectLabTest