import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import './newAppointment.scss'

import { APPOINTMENTS_CREATE_API, DOCTORS_ALL_API, PATIENTS_ONE_API, SERVICES_ALL_API } from '../../components/constants'
import { Navbar } from '../../components/navbar/Navbar'
import { Sidebar } from '../../components/sidebar/Sidebar'
import { failureNotify, successNotify } from '../../components/toaster/toastNotification'
import { getBearerHeaderValue } from '../../service/userservice'

const NewAppointment = () => {

    const id = useParams().patientId;

    const [patient, setPatient] = useState([]);
    const [doctors, setDoctors] = useState([]);
    const [services, setServices] = useState([]);

    const getPatient = async () => {
        const response = axios.get(PATIENTS_ONE_API.replace('{id}', id),
            {
                headers: {
                    'Authorization': getBearerHeaderValue()
                }
            }).then(
                res => {
                    setPatient(res.data.data);
                }
            ).catch(err => {
                console.log(err);
            });
    }

    const getDoctors = async () => {
        const response = axios.get(DOCTORS_ALL_API,
            {
                headers: {
                    'Authorization': getBearerHeaderValue()
                }
            }).then(
                res => {
                    setDoctors(res.data.data);
                }
            ).catch(err => {
                console.log(err);
            });
    }

    const getServices = async () => {
        const response = axios.get(SERVICES_ALL_API,
            {
                headers: {
                    'Authorization': getBearerHeaderValue()
                }
            }).then(
                res => {
                    setServices(res.data.data);
                }
            ).catch(err => {
                console.log(err);
            });
    }

    useEffect(() => {
        getPatient()
        getDoctors()
        getServices()
    }, []);

    const [serviceId, setServiceId] = useState(0);
    const [doctorId, setDoctorId] = useState(0);
    const [dateTime, setDateTime] = useState(null);
    const [remarks, setRemarks] = useState(null);


    function save(event) {
        event.preventDefault();
        let item = {
            patient: {
                id: id
            },
            hospitalService: {
                id: serviceId
            },
            staff: {
                id: doctorId
            },
            dateTime: dateTime,
            remarks: remarks
        };

        const response = axios.post(APPOINTMENTS_CREATE_API, item,
            {
                headers: {
                    'Authorization': getBearerHeaderValue()
                }
            }).then(
                res => {
                    successNotify('Booking Created!');
                }
            ).catch(err => {
                console.log(err);
                failureNotify('Booking failed!');
            });
    }

    return (
        <div>
            <div className='new'>
                <Sidebar />
                <div className="newContainer">
                    <Navbar />
                    <div className="top">
                        <h1>Book Appointment</h1>
                    </div>
                    <div className="bottom">

                        <div className="left">
                            <form onSubmit={save}>

                                <div className="formInput">
                                    <label>Doctor</label>
                                    
                                    <select onChange={(e) => {
                                        setDoctorId(e.target.value)
                                    }}>
                                        <option value='0'>Select Doctor</option>
                                        {doctors.map((item, index) => {
                                            return <option value={item.id}>{item.name}</option>
                                        })}
                                    </select>
                                </div>

                                <div className="formInput">
                                    <label>Service</label>
                                    <select onChange={(e) => {
                                        setServiceId(e.target.value)
                                    }}>
                                        <option value='0'>Select Service</option>
                                        {services.map((item, index) => {
                                            return <option value={item.id}>{item.name}</option>
                                        })}
                                    </select>
                                </div>

                                <div className="formInput">
                                    <label>Date Time</label>
                                    <input type="datetime-local" onSelect={(e) => {
                                        setDateTime(e.target.value)
                                    }}
                                    />

                                </div>
                                <div className="formInput">
                                    <label>Remarks</label>
                                    <input type="text" placeholder="Enter Remarks" onKeyUp={(e) => {
                                        setRemarks(e.target.value)
                                    }}
                                    />
                                </div>

                                <button>Book</button>

                            </form>
                        </div>
                        <div className="right">

                            <div className="details">
                                <h1 className="itemTitle">
                                    Patient Info
                                </h1>
                                <div className="detailItem">
                                    <span className="itemKey">Name:</span>
                                    <span className="itemValue">{patient.name}</span>
                                </div>
                                <div className="detailItem">
                                    <span className="itemKey">Email:</span>
                                    <span className="itemValue">{patient.email}</span>
                                </div>
                                <div className="detailItem">
                                    <span className="itemKey">Phone:</span>
                                    <span className="itemValue">{patient.phone}</span>
                                </div>
                                <div className="detailItem">
                                    <span className="itemKey">DOB:</span>
                                    <span className="itemValue">{patient.dob}</span>
                                </div>
                            </div>
                        </div>
                        
                    </div>
                </div >
            </div >
            <ToastContainer />
        </div>
    )
}

export default NewAppointment