import { width } from '@mui/system';
import React, { useState } from 'react'
import { useEffect } from 'react';
import axios from 'axios'
import moment from 'moment';

import Datatable from '../../components/datatable/Datatable'

import { APPOINTMENTS_ALL_API } from '../../components/constants.js'
import { getBearerHeaderValue } from '../../service/userservice';

const tableHeaders = [
    { field: 'id', headerName: 'ID', width: 70 },
    {
        field: 'hospitalService.name', headerName: 'Service', width: 130,
        renderCell: (params) => {
            return (
                params.row.hospitalService.name
            )
        }
    },
    {
        field: 'staff.name', headerName: 'Doctor', width: 130,
        renderCell: (params) => {
            return (
                params.row.staff.name
            )
        }
    },
    {
        field: 'dateTime',
        headerName: 'Appointment Date Time',
        sortable: true,
        width: 200,
        renderCell: (params) => {
            const date= moment(params.dateTime).format('dd-MM-yyyy h:mma');
            const formatDate = date;
            return (
                formatDate
            )
        }
    }
];

const AppointmentsDataTable = () => {
    const [appointments, setAppointments] = useState([]);

    const getAppointments = async () => {
        const response = axios.get(APPOINTMENTS_ALL_API,
            {
                headers: {
                    'Authorization': getBearerHeaderValue()
                }
            }).then(
                res => {
                    setAppointments(res.data.data);
                }
            ).catch(err => {
                console.log(err);
            });
    }

    useEffect(() => {
        getAppointments()
    }, []);

    return (
        <div className='appointmentDataTable'>
            <Datatable columns={tableHeaders} rows={appointments} />
        </div>
    )
}

export default AppointmentsDataTable