import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import Datatable from '../../components/datatable/Datatable'
import './newLabBooking.scss'

import { LAB_BOOKING_CREATE_API, PATIENTS_ONE_API } from '../../components/constants'
import { Navbar } from '../../components/navbar/Navbar'
import { Sidebar } from '../../components/sidebar/Sidebar'
import { failureNotify, successNotify } from '../../components/toaster/toastNotification'
import { getBearerHeaderValue } from '../../service/userservice'

import SelectLabTest from '../../components/core/SelectLabTest'

const tableHeaders = [
    { field: 'id', headerName: 'ID', width: 150 },
    { field: 'testName', headerName: 'Test Name', width: 150 },
    {
        field: 'min', headerName: 'Minimum', width: 130
    },
    {
        field: 'max', headerName: 'Maximum', width: 130
    },
    {
        field: 'unit',
        headerName: 'Unit',
        width: 200
    }
];

const NewLabBooking = () => {
    const id = useParams().patientId;
    const [patient, setPatient] = useState([]);
    const [dateTime, setDateTime] = useState(null);
    const [remarks, setRemarks] = useState(null);
    const [status, setStatus] = useState('IN_PROGRESS');

    const [addedLabTests, setAddedLabTests] = useState([]);
    const [labTestsForDataTable, setLabTestsForDataTable] = useState([]);

    const [labTest, setLabTest] = useState({});
    const handleChange = value => {
        setLabTest(value);
    }

    const handleAddTest = () => {
        let labTestPayload = {
            coreLabTestDetail: labTest
        }
        setAddedLabTests(addedLabTests => [...addedLabTests, labTestPayload]);
        setLabTestsForDataTable(addedLabTests => [...addedLabTests, labTest]);
    }

    const getAddedLabTests = () => {
        return addedLabTests;
    }

    const getPatient = async () => {
        const response = axios.get(PATIENTS_ONE_API.replace('{id}', id),
            {
                headers: {
                    'Authorization': getBearerHeaderValue()
                }
            }).then(
                res => {
                    setPatient(res.data.data);
                }
            ).catch(err => {
                console.log(err);
            });
    }


    useEffect(() => {
        getPatient();
    }, []);

    function save(event) {
        event.preventDefault();


        let item = {
            patient: {
                id: id
            },
            bookingDateTime: dateTime,
            remarks: remarks,
            status: status,
            labBookingDetails: addedLabTests
        };

        const response = axios.post(LAB_BOOKING_CREATE_API, item,
            {
                headers: {
                    'Authorization': getBearerHeaderValue()
                }
            }).then(
                res => {
                    successNotify('Booking Created!');
                }
            ).catch(err => {
                console.log(err);
                failureNotify('Booking failed!');
            });
    }

    return (
        <div>
            <div className='new'>
                <Sidebar />
                <div className="newContainer">
                    <Navbar />
                    <div className="top">
                        <h1>Book Lab Service</h1>
                    </div>
                    <div className="bottom">

                        <div className="left">
                            <form onSubmit={save}>
                                <div className="formInput">
                                    <label>Test Name</label>
                                    <SelectLabTest handleChange={handleChange} sharedValue={labTest} />
                                </div>
                                <div className="formInput">
                                    <a onClick={handleAddTest} className="addTestBtn">Add Test</a>
                                </div>
                                <div className="formInput">
                                    <label>Date Time</label>
                                    <input type="datetime-local" onChange={(e) => {
                                        setDateTime(e.target.value)
                                    }}
                                    />

                                </div>
                                <div className="formInput">
                                    <label>Remarks</label>
                                    <input type="text" placeholder="Enter Remarks" onKeyUp={(e) => {
                                        setRemarks(e.target.value)
                                    }}
                                    />
                                </div>

                                <div className="formInput">
                                    <label>Status</label>
                                    <select onChange={(e) => {
                                        setStatus(e.target.value)
                                    }}>
                                        <option value="IN_PROGRESS">In Progress</option>
                                        <option value="COMPLETED">Completed</option>
                                    </select>

                                </div>
                                <div className="formInput"></div>
                                <button type='submit'>Book</button>
                            </form>

                        </div>
                        <div className="right">

                            <div className="details">
                                <h1 className="itemTitle">
                                    Patient Info
                                </h1>
                                <div className="detailItem">
                                    <span className="itemKey">Name:</span>
                                    <span className="itemValue">{patient.name}</span>
                                </div>
                                <div className="detailItem">
                                    <span className="itemKey">Email:</span>
                                    <span className="itemValue">{patient.email}</span>
                                </div>
                                <div className="detailItem">
                                    <span className="itemKey">Phone:</span>
                                    <span className="itemValue">{patient.phone}</span>
                                </div>
                                <div className="detailItem">
                                    <span className="itemKey">DOB:</span>
                                    <span className="itemValue">{patient.dob}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="lowerBottom">
                        <Datatable columns={tableHeaders} rows={labTestsForDataTable} />
                    </div>
                </div >
            </div >
            <ToastContainer />
        </div>
    )
}

export default NewLabBooking