import {
  BrowserRouter, Route, Routes
} from "react-router-dom";
import Appointments from "./pages/appointments/Appointments";
import NewAppointment from "./pages/appointments/NewAppointment";
import Doctors from "./pages/doctors/Doctors";
import DoctorSingle from "./pages/doctors/DoctorSingle";
import NewDoctor from "./pages/doctors/NewDoctor";
import Home from "./pages/home/Home";
import LabBookings from "./pages/labs/LabBookings";
import LabBookingUpdate from "./pages/labs/LabBookingUpdate";
import NewLabBooking from "./pages/labs/NewLabBooking";
import Login from "./pages/login/Login";
import NewPatient from "./pages/patients/NewPatient";
import Patients from "./pages/patients/Patients";
import PatientSingle from "./pages/patients/PatientSingle";

function App() {
  const isLoggedIn = true;

  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" >
          <Route index element={<Login />}></Route>

          <Route path="dashboard" element={<Home />}></Route>

          <Route path="appointments">
            <Route index element={<Appointments />}></Route>
            <Route path=":appointmentId" element={<Appointments />}></Route>
            <Route path="new">
              <Route path=":patientId" element={<NewAppointment />}></Route>
            </Route>
          </Route>

          <Route path="patients">
            <Route index element={<Patients />}></Route>
            <Route path=":patientId" element={<PatientSingle />}></Route>
            <Route path="new" element={<NewPatient />}></Route>
          </Route>

          <Route path="doctors">
            <Route index element={<Doctors />}></Route>
            <Route path=":doctorId" element={<DoctorSingle />}></Route>
            <Route path="new" element={<NewDoctor />}></Route>
          </Route>

          <Route path="labs">
            <Route index element={<LabBookings />}></Route>
            <Route path=":bookingId" element={<LabBookingUpdate />}></Route>
            <Route path="new">
              <Route path=":patientId" element={<NewLabBooking />}></Route>
            </Route>
          </Route>
        </Route>
      </Routes>
    </BrowserRouter>
  );
}

export default App;
